<template>
    <div id="divGanttView">
        <loader :is-visible="isLoading"></loader>
        <div class="panel">
            <div class="panel-content p-3 pb-2 pagination2 table-responsive no-padding switchcalendar border-0">
                <div class="control-section">                   
                    <div class="content-wrapper row">
                        <div class="schedule-container e-schedule schedule-drag-drop px-3">
                            <ejs-schedule ref='ScheduleObj' id='Schedule' width='100%' height='550px'  :eventSettings='eventSettings'
                                :selectedDate='selectedDate' :currentView='IsView' :actionComplete='onActionComplete'  
                                @eventClick="onEventClick"                                
                                :eventTemplate="'eventTemplate'">
                                <e-views>
                                    <e-view :showWeekend='showWeekend' option='Month' showWeekNumber=true readonly=true ></e-view>
                                </e-views>
                            </ejs-schedule>
                        </div>                        
                        <div class="treeview-container px-3">
                            <div class="title-container">
                                <h1 class="title-text d-flex align-items-center">
                                    <span class="w-100 text-left">{{ $t('UnscheduledList') }}</span>
                                    <span class="select-in-tag">
                                        <input v-model="txtSearchAppointmentNo" class="form-control input-sm"
                                            placeholder="Search by Appt. no." type="text" style="margin-right:100px;"
                                            @input="GetUnscheduleList($event, 'keypress')">
                                    </span>
                                </h1>
                            </div>
                            <div class="treeview-component" v-if="UnSchduleList.length > 0">
                                <ejs-treeview id='Tree' ref='TreeViewScheduleObj' cssClass="treeview-external-drag"
                                    :nodeTemplate="treeTemplate" :fields='treeViewFields'
                                    :nodeSelecting="onItemSelecting">
                                </ejs-treeview>
                            </div>
                            <div v-else class="text-danger text-center py-4 border">{{ $t('NoRecordfound') }}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <SchduleAppointment v-if="IsSchduleAppointment" :SchduleAppointment="SchduleAppointment"
            :SA_GroupData="SA_GroupData" :IsCurrentView="CurrentView"></SchduleAppointment>
    </div>   
</template>
<script>
import { ScheduleComponent,Month,ViewDirective,ViewsDirective } from "@syncfusion/ej2-vue-schedule";
import { TreeViewComponent } from "@syncfusion/ej2-vue-navigations";
import moment from 'moment';
import ToolTip from './ToolTipTemplate.vue'
import SchduleAppointment from './SchduleAppointment.vue'
import EventTemplate from './eventTemplate.vue'
import dateHeaderTemplate from './DateHeader.vue'
import DataService from '../../services/DataService';
import TreeVue from './TreeView.vue'
export default {
    components: {
        'ejs-schedule': ScheduleComponent,
        'e-view': ViewDirective,
        'e-views': ViewsDirective,
        'ejs-treeview': TreeViewComponent, ToolTip ,EventTemplate,dateHeaderTemplate,TreeVue,SchduleAppointment
    },
    data(){
        return{
            isLoading:false,
            SA_GroupData: [],
            CurrentView: 'mixed',
            IsSchduleAppointment:false,
            selectedDate: new Date(),
            IsView: 'Month',
            UnSchduleList: [],
            rowAutoHeight: true,
            txtSearchAppointmentNo:'',
            dateHeaderTemplate: function (e) {
                return {
                    template: dateHeaderTemplate
                };
            },
            group: {
            },
            workDays: [],
            showWeekend: false,
            eventSettings: {
                dataSource: [],
                enableTooltip: true,
                editFollowingEvents: true,
                enableIndicator: false,
                enableMaxHeight: true,
                tooltipTemplate: function (e) {
                    return {
                        template: ToolTip
                    };
                },
                eventTemplate: function (e) {
                    return {
                        template: EventTemplate
                    };
                },
            },
            treeViewFields: {
                dataSource: [],
                id: 'SA_Id',
                text: 'Name'
            },
            treeTemplate: function (e) {
                return { template: TreeVue };
            },
        }
    },
    provide: {
            schedule: [Month]
    },
    created:async function(){
        await this.IsExtraMapFeature();
    },
    methods:{       
        onAppointmentClick: function (args) {
            args.cancel = true;
        },
        onItemSelecting: function (args) {
            args.cancel = true;
        },
        getCurrentViewTimeline() {
            try {
                var value = localStorage.getItem('timeline');
                return (value != null) ? value : 'resource';
            }
            catch {
                return 'resource';
            }
        },
        onEventClick(args) {
            debugger
            args.cancel = true;
            if (args.event.SA_Status != "Canceled") {
                this.openScheduledAppointmentPopupForEdit(args.event.Id);
            }
            else {
                this.ShowAlert(this.$t('CancelledSA', [this.$t('ServiceAppointment')]), "Success", true, this.$t('Alert'));
            }
        },
        openScheduledAppointmentPopupForEdit(val) {            
            var url = `id=${val}&isForTickets=${this.ExtraMapFeature}`
            DataService.GetSADetailById(url).then(response => {  
                var data = response.data.result;
                this.openScheduledAppointmentPopup(data);
            });
        },
        openScheduledAppointmentPopup: function (e) {            
            if (e) {
                if (typeof e.groupId != 'undefined' && e.groupId != null) {
                    this.SA_GroupData = [];
                    this.SA_GroupData.push({ value: parseInt(e.groupId), name: e.groupName, type: e.groupType })
                }
                this.SchduleAppointment = e;
                this.IsSchduleAppointment = true;
            }
        },
        IsExtraMapFeature: async function () {
            await DataService.IsExtraMapFeature().then(response => {
                if (response.data == 'locating') {
                    this.ExtraMapFeature = true;
                    this.removeViewWithValue(this.ddlCurrentView, this.ExtraMapFeature);
                }
                else {
                    this.ExtraMapFeature = false;
                }
            });
        },
        removeViewWithValue(arr, id) {
            if (id) {
                const objWithIdIndex = arr.findIndex((obj) => obj.value === 'customer');
                if (objWithIdIndex > -1) {
                    arr.splice(objWithIdIndex, 1);
                }
                return arr;
            }
        },
        onActionComplete(args) {
            var ths = this;
            if (typeof(ths.$refs.ScheduleObj.ej2Instances.activeView) != 'undefined' || ths.$refs.ScheduleObj.ej2Instances.activeView != null) {
                var monthDates = ths.$refs.ScheduleObj.ej2Instances.activeView.monthDates;
                ths.startDate = ths.$refs.ScheduleObj.ej2Instances.activeView.monthDates.start.toLocaleDateString();
                ths.lastDate = ths.$refs.ScheduleObj.ej2Instances.activeView.monthDates.end.toLocaleDateString();
                
            }else{
                ths.startDate = moment().startOf('month').format('MM/DD/YYYY');
                ths.lastDate = moment().endOf('month').format('MM/DD/YYYY');
            }
            ths.workDays=ths.$refs.ScheduleObj.ej2Instances.workDays;
            ths.refreshCalenderDataForNU();
            ths.GetUnscheduledServiceAppointmentList();
        },
        getCurrentView: function () {            
            try {
                var value = localStorage.getItem('view');
                return (value != null) ? value : 'resource';
            }
            catch {
                return 'resource';
            }
        },
        refreshCalenderDataForNU:async function(){            
            var ths = this;
            ths.isLoading = true;
            ths._startDate = ths.startDate;
            ths._lastDate = ths.lastDate;
            var url = `viewType=${ths.getCurrentView()}&currentdate=${ths._startDate}&lastdate=${ths._lastDate}&isFirstTime=true&filters=&timezone_id=`;
            await DataService.GetSACalenderList(url).then(response => {                
                if (response.data.status === "Success") {
                    this.dataCalenderSource = response.data.result;
                    if (this.getCurrentView() === "crew") {
                        var _data = [];
                        this.dataCalenderSource.forEach(m => {
                            if (m.IsCrew == 1) {
                                m = Object.keys(m).filter(function (key) {
                                    return key !== 'EmployeeId';
                                }).reduce(function (obj, key) {
                                    obj[key] = m[key];
                                    return obj;
                                }, {});
                                _data.push(m);
                            }
                            else {
                                _data.push(m);
                            }
                        });
                        ths.eventSettings = { dataSource: [..._data] };
                    } else {                        
                        ths.eventSettings = { dataSource: [...this.dataCalenderSource] };
                    }
                    ths.isLoading=false;
                }
            });
        },
        GetUnscheduleList: function (e, type) {
            var keyCode = e.keyCode || e.which;
            var value = this.txtSearchAppointmentNo.toLowerCase();
            if (keyCode == 35 || keyCode == 36 || keyCode == 38) {
                this.ShowAlert('Not a valid character.', "warning", this.$t('Alert'));
                this.GetUnscheduledServiceAppointmentList();
            }
            else if (/^[a-zA-Z0-9- ]*$/.test(value) == false) {
                this.ShowAlert('Please enter valid characters.', "warning", this.$t('Alert'));
                this.GetUnscheduledServiceAppointmentList();
            }
            else {
                var value = this.txtSearchAppointmentNo;
                this.GetUnscheduledServiceAppointmentList(value);
            }
        },
        GetUnscheduledServiceAppointmentList: async function (type) {
            var Unschduletype = type == undefined ? '' : type;
            var url = `search=${Unschduletype}`;
            await DataService.GetUnscheduledServiceAppointmentList(url).then(response => {             
                this.UnSchduleList = response.data.result;
                if (this.UnSchduleList.length > 0) {
                    var Obj = {
                        dataSource: this.UnSchduleList,
                        id: 'SA_Id', text: 'Name'
                    }
                    this.treeViewFields = Obj;
                }
            });
        },
    }
}
</script>
